"use client";
import { useEffect, useState } from "react";
import Image from "next/image";

interface Business {
  _id: string;
  name: string;
  category: string;
  description: string;
}

const ITEMS_PER_PAGE = 9;

export default function Landing() {
  const [businesses, setBusinesses] = useState<Business[]>([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);

  const fetchBusinesses = async (page: number) => {
    try {
      const res = await fetch(`http://localhost:5000/api/business/viewBizs?page=${page}&limit=${ITEMS_PER_PAGE}`);
      const data = await res.json();
      setBusinesses(data.businesses);
      setTotalPages(data.totalPages);
    } catch (err) {
      console.error("Error fetching businesses:", err);
    }
  };

  useEffect(() => {
    fetchBusinesses(currentPage);
  }, [currentPage]);

  return (
    <main className="p-6">
      <h1 className="text-3xl font-bold mb-6 text-center">Business Directory</h1>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {businesses.map((biz) => (
          <div key={biz._id} className="border rounded-lg shadow-md bg-white overflow-hidden">
            <Image
              src="/default-banner-img.png"
              alt={`${biz.name} banner`}
              width={600}
              height={200}
              className="w-full h-40 object-cover"
              priority
            />
            <div className="p-4 flex items-start gap-4">
              <Image
                src="/default-logo.png"
                alt={`${biz.name} logo`}
                width={64}
                height={64}
                className="rounded-full object-cover"
              />
              <div>
                <h2 className="text-xl text-zinc-800 font-semibold">{biz.name}</h2>
                <p className="text-sm text-gray-500">{biz.category}</p>
                <p className="text-gray-700 mt-1">{biz.description}</p>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Pagination Controls */}
      <div className="flex justify-center mt-8 gap-4">
        <button
          onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
          disabled={currentPage === 1}
          className="px-4 py-2 bg-gray-200 rounded disabled:opacity-50 text-zinc-800"
        >
          Previous
        </button>
        <span className="text-lg font-medium">
          Page {currentPage} of {totalPages}
        </span>
        <button
          onClick={() => setCurrentPage((prev) => Math.min(prev + 1, totalPages))}
          disabled={currentPage === totalPages}
          className="px-4 py-2 bg-gray-200 rounded disabled:opacity-50 text-zinc-800"
        >
          Next
        </button>
      </div>
    </main>
  );
}
